import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { WorkflowTemplate, TemplateCategory } from '@/lib/accelerator/types'

// ==========================================
// GET /api/v1/workflows/templates - List workflow templates
// ==========================================
export async function GET(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const subAccountId = searchParams.get('subAccountId')
    const templateId = searchParams.get('id')
    const category = searchParams.get('category') as TemplateCategory | null
    const search = searchParams.get('search')
    const featured = searchParams.get('featured') === 'true'
    const page = parseInt(searchParams.get('page') || '1')
    const limit = Math.min(parseInt(searchParams.get('limit') || '20'), 100)

    // If specific template requested
    if (templateId) {
      const { data: template, error } = await supabase
        .from('workflow_templates')
        .select('*')
        .eq('id', templateId)
        .single()

      if (error || !template) {
        return NextResponse.json(
          { success: false, error: 'Template not found' },
          { status: 404 }
        )
      }

      return NextResponse.json({
        success: true,
        data: transformTemplate(template)
      })
    }

    // List templates
    let query = supabase
      .from('workflow_templates')
      .select('*', { count: 'exact' })
      .order('is_featured', { ascending: false })
      .order('usage_count', { ascending: false })

    // Include public templates and user's own templates
    if (subAccountId) {
      query = query.or(`is_public.eq.true,sub_account_id.eq.${subAccountId}`)
    } else {
      query = query.eq('is_public', true)
    }

    if (category) {
      query = query.eq('category', category)
    }

    if (featured) {
      query = query.eq('is_featured', true)
    }

    if (search) {
      query = query.or(`name.ilike.%${search}%,description.ilike.%${search}%`)
    }

    // Pagination
    const from = (page - 1) * limit
    const to = from + limit - 1
    query = query.range(from, to)

    const { data: templates, error, count } = await query

    if (error) {
      console.error('Error fetching templates:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to fetch templates' },
        { status: 500 }
      )
    }

    const transformedTemplates = (templates || []).map(transformTemplate)

    return NextResponse.json({
      success: true,
      data: transformedTemplates,
      pagination: {
        page,
        limit,
        total: count || 0,
        totalPages: Math.ceil((count || 0) / limit)
      }
    })

  } catch (error) {
    console.error('Error in GET /api/v1/workflows/templates:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/workflows/templates - Create template from workflow
// ==========================================
export async function POST(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      workflowId,
      subAccountId,
      name,
      description,
      category,
      tags = [],
      isPublic = false,
      createdBy
    } = body

    // If creating from existing workflow
    if (workflowId) {
      const { data: workflow, error: workflowError } = await supabase
        .from('workflows')
        .select(`
          *,
          workflow_actions (*)
        `)
        .eq('id', workflowId)
        .single()

      if (workflowError || !workflow) {
        return NextResponse.json(
          { success: false, error: 'Workflow not found' },
          { status: 404 }
        )
      }

      const templateId = uuidv4()
      const { data: template, error: templateError } = await supabase
        .from('workflow_templates')
        .insert({
          id: templateId,
          name: name || workflow.name,
          description: description || workflow.description,
          category: category || 'follow_up',
          trigger_type: workflow.trigger_type,
          trigger_config: workflow.trigger_config,
          actions_config: workflow.workflow_actions?.map((a: any) => ({
            type: a.action_type,
            name: a.name,
            config: a.config,
            delay_minutes: a.delay_minutes,
            conditions: a.conditions
          })) || [],
          required_fields: [],
          optional_fields: [],
          tags,
          is_public: isPublic,
          sub_account_id: subAccountId,
          created_by: createdBy
        })
        .select()
        .single()

      if (templateError) {
        console.error('Error creating template:', templateError)
        return NextResponse.json(
          { success: false, error: 'Failed to create template' },
          { status: 500 }
        )
      }

      return NextResponse.json({
        success: true,
        data: transformTemplate(template)
      }, { status: 201 })
    }

    // Create template from scratch
    if (!name || !category) {
      return NextResponse.json(
        { success: false, error: 'name and category are required' },
        { status: 400 }
      )
    }

    const {
      triggerType,
      triggerConfig = {},
      actionsConfig = [],
      requiredFields = [],
      optionalFields = []
    } = body

    const templateId = uuidv4()
    const { data: template, error: templateError } = await supabase
      .from('workflow_templates')
      .insert({
        id: templateId,
        name,
        description,
        category,
        trigger_type: triggerType || 'contact_created',
        trigger_config: triggerConfig,
        actions_config: actionsConfig,
        required_fields: requiredFields,
        optional_fields: optionalFields,
        tags,
        is_public: isPublic,
        sub_account_id: subAccountId,
        created_by: createdBy
      })
      .select()
      .single()

    if (templateError) {
      console.error('Error creating template:', templateError)
      return NextResponse.json(
        { success: false, error: 'Failed to create template' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformTemplate(template)
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/workflows/templates:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/workflows/templates/[id]/use - Create workflow from template
// ==========================================
export async function PUT(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      templateId,
      subAccountId,
      name,
      customizations = {},
      createdBy
    } = body

    if (!templateId || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'templateId and subAccountId are required' },
        { status: 400 }
      )
    }

    // Get template
    const { data: template, error: templateError } = await supabase
      .from('workflow_templates')
      .select('*')
      .eq('id', templateId)
      .single()

    if (templateError || !template) {
      return NextResponse.json(
        { success: false, error: 'Template not found' },
        { status: 404 }
      )
    }

    // Create workflow from template
    const workflowId = uuidv4()
    const { data: workflow, error: workflowError } = await supabase
      .from('workflows')
      .insert({
        id: workflowId,
        sub_account_id: subAccountId,
        name: name || template.name,
        description: template.description,
        status: 'draft',
        trigger_type: template.trigger_type,
        trigger_config: { ...template.trigger_config, ...customizations.triggerConfig },
        trigger_filters: [],
        allow_multiple_executions: true,
        version: 1,
        created_by: createdBy
      })
      .select()
      .single()

    if (workflowError) {
      console.error('Error creating workflow from template:', workflowError)
      return NextResponse.json(
        { success: false, error: 'Failed to create workflow' },
        { status: 500 }
      )
    }

    // Create actions from template
    const actionsConfig = template.actions_config || []
    if (actionsConfig.length > 0) {
      const actionsToInsert = actionsConfig.map((action: any, index: number) => ({
        id: uuidv4(),
        workflow_id: workflowId,
        action_type: action.type,
        name: action.name || `Action ${index + 1}`,
        config: { ...action.config, ...(customizations.actions?.[index]?.config || {}) },
        delay_minutes: action.delay_minutes || 0,
        continue_on_error: action.continue_on_error || false,
        execution_order: index,
        conditions: action.conditions || []
      }))

      await supabase
        .from('workflow_actions')
        .insert(actionsToInsert)
    }

    // Increment template usage count
    await supabase
      .from('workflow_templates')
      .update({ usage_count: template.usage_count + 1 })
      .eq('id', templateId)

    // Fetch complete workflow
    const { data: completeWorkflow } = await supabase
      .from('workflows')
      .select(`
        *,
        workflow_actions (*)
      `)
      .eq('id', workflowId)
      .single()

    return NextResponse.json({
      success: true,
      data: {
        id: completeWorkflow.id,
        name: completeWorkflow.name,
        status: completeWorkflow.status,
        createdFromTemplate: templateId
      }
    }, { status: 201 })

  } catch (error) {
    console.error('Error in PUT /api/v1/workflows/templates:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

function transformTemplate(dbTemplate: any): WorkflowTemplate {
  return {
    id: dbTemplate.id,
    name: dbTemplate.name,
    description: dbTemplate.description,
    category: dbTemplate.category,
    trigger: {
      type: dbTemplate.trigger_type,
      config: dbTemplate.trigger_config || {}
    },
    actions: dbTemplate.actions_config || [],
    requiredFields: dbTemplate.required_fields || [],
    optionalFields: dbTemplate.optional_fields || [],
    thumbnailUrl: dbTemplate.thumbnail_url,
    previewImages: dbTemplate.preview_images || [],
    usageCount: dbTemplate.usage_count,
    rating: dbTemplate.rating,
    tags: dbTemplate.tags || [],
    isPublic: dbTemplate.is_public,
    createdAt: dbTemplate.created_at,
    updatedAt: dbTemplate.updated_at,
    createdBy: dbTemplate.created_by
  }
}
